import { request, requestDownload } from 'utils';

const prefix = '/api/finance';
const studentPrefix = '/api/students';

export const getFeeObjectStudent = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  page,
}) => {
  const defaultValue = {
    pageSize: 10,
    isPagingEnabled: true,
  };
  const paramSearch = {
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    page,
    ...defaultValue,
  };
  return request({
    prefix: studentPrefix,
    url: '/Student/Search',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });
};

export const getFeeObjectStudentAdded = ({ yearEduId, semesterId, feeObjectId, page }) => {
  const defaultValue = {
    pageSize: 10,
    isPagingEnabled: true,
  };
  const paramSearch = {
    yearEduId,
    semesterId,
    feeObjectId,
    page,
    ...defaultValue,
  };
  return request({
    prefix,
    url: '/FeeObject_Student',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });
};

export const addStudentAdded = async reqs => {
  await request({
    prefix,
    url: '/FeeObject_Student',
    method: 'POST',
    params: {},
    data: reqs,
  });
};

export const removeStudentAdded = async reqs => {
  await request({
    prefix,
    url: '/FeeObject_Student',
    method: 'DELETE',
    params: {},
    data: reqs,
  });
};

export const exportExcel = async ({ yearEduId, semesterId, feeObjectId }) =>
  requestDownload({
    defaultFileName: 'Đối tượng học phí.xlsx',
    prefix,
    url: `/FeeObject_Student/ExportFeeObjectStudentToExcel`,
    method: 'POST',
    data: { yearEduId, semesterId, feeObjectId },
  });

export const exportPdf = async ({ yearEduId, semesterId, feeObjectId }) =>
  requestDownload({
    defaultFileName: 'Đối tượng học phí.pdf',
    prefix,
    url: `/FeeObject_Student/ExportFeeObjectStudentToPdf`,
    method: 'POST',
    data: { yearEduId, semesterId, feeObjectId },
  });



