import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { requiredRule } from 'utils/validateRules';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const { lop, trainTypeId, facultyId, courseId, studyFieldId } = getQueryParams();
    form.setFieldsValue({ lop, trainTypeId, facultyId, courseId, studyFieldId });
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lớp A',
        },
        {
          id: '2',
          label: 'Lớp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams({ ...this.props.paramSearch, ...values });
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <FilterPathEducate
            form={this.props.form}
            optionFieldDecorator={{
              trainTypeId: {
                rules: [requiredRule],
              },
              facultyId: {
                rules: [requiredRule],
              },
              courseId: {
                rules: [requiredRule],
              },
            }}
          />
        </Row>
        <Row gutter={24}>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



