import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Btn, BtnAddNew, BtnDelete, BtnExport } from 'components/common/button';
import {
  getFeeObjectStudent,
  getFeeObjectStudentAdded,
  addStudentAdded,
  removeStudentAdded,
  exportExcel,
  exportPdf,
} from 'services/feeObjectStudent';
import { getQueryParams } from 'utils/urlUtil';
import { Card, Row, Icon, message } from 'antd';
import TableStudentData from './TableStudentData';
import TableStudentDataAdded from './TableStudentDataAdded';
import SearchBar from './SearchBar';
import SearchBarStudentAdded from './SearchBarStudentAdded';
import './style.scss';

class FeeObjectStudent extends Component {
  state = {
    paramSearch: {},
    studentSelected: {},
    studentSelectedRowKey: [],
    studentAddedSelected: {},
    studentAddedSelectedRowKey: [],
    reloadTable: 0,
    reloadTableAdded: 0,
    hasLoadedAdded: false,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getFeeObjectStudentAdded = async paramSearch => {
    const res = await getFeeObjectStudentAdded(paramSearch);
    const hasLoadedAdded = res.data && res.data.items;
    this.setState({
      hasLoadedAdded,
    });
    return res;
  };

  handleChangeQueryParams = () => {
    this.setState({
      paramSearch: getQueryParams(),
    });
  };

  updateStudentKey = (student, subjectIndex) => {
    student.key = `${student.masinhvien} - ${subjectIndex}`;
    return student;
  };

  addStudent = async () => {
    const { reloadTable, reloadTableAdded, studentSelected, paramSearch } = this.state;
    const { yearEduId, semesterId, feeObjectId } = paramSearch;
    try {
      const reqs = Object.keys(studentSelected).map(studentId => {
        const req = {
          studentId,
          yearEduId,
          semesterId,
          feeObjectId,
        };
        return req;
      });
      await addStudentAdded(reqs);
      this.setState({
        studentSelectedRowKey: [],
        studentAddedSelectedRowKey: [],
        reloadTable: reloadTable + 1,
        reloadTableAdded: reloadTableAdded + 1,
        studentSelected: {},
      });
      message.success('Thêm SV thành công');
    } catch (err) {
      message.error('Thêm SV không thành công');
    }
  };

  removeStudent = async () => {
    const { reloadTable, reloadTableAdded, studentAddedSelected, paramSearch } = this.state;
    const { yearEduId, semesterId, feeObjectId } = paramSearch;
    try {
      const reqs = Object.keys(studentAddedSelected).map(studentId => {
        const req = {
          studentId,
          yearEduId,
          semesterId,
          feeObjectId,
        };
        return req;
      });
      await removeStudentAdded(reqs);
      this.setState({
        studentSelectedRowKey: [],
        studentAddedSelectedRowKey: [],
        reloadTable: reloadTable + 1,
        reloadTableAdded: reloadTableAdded + 1,
        studentAddedSelected: {},
      });
      message.success('Xóa SV thành công');
    } catch (err) {
      message.error('Xóa SV không thành công');
    }
  };

  handleExportExcel = async () => {
    const { paramSearch } = this.state;
    const { yearEduId, semesterId, feeObjectId } = paramSearch;
    await exportExcel({ yearEduId, semesterId, feeObjectId });
  };

  handleExportPdf = async () => {
    const { paramSearch } = this.state;
    const { yearEduId, semesterId, feeObjectId } = paramSearch;
    await exportPdf({ yearEduId, semesterId, feeObjectId });
  };

  render() {
    const {
      reloadTable,
      reloadTableAdded,
      paramSearch,
      studentSelected,
      studentAddedSelected,
      studentSelectedRowKey,
      studentAddedSelectedRowKey,
      hasLoadedAdded,
    } = this.state;

    const { trainTypeId, facultyId, courseId, yearEduId, semesterId, feeObjectId } = paramSearch;

    const studentSelection = {
      selectedRowKeys: studentSelectedRowKey,
      onChange: (selectedRowKeys, selectedRows) => {
        const studentSelectedTmp = {};

        selectedRows.forEach(selectedRow => {
          studentSelectedTmp[selectedRow.studentId] = selectedRow;
        });

        this.setState({
          studentSelected: studentSelectedTmp,
          studentSelectedRowKey: selectedRowKeys,
        });
      },
    };

    const studentAddedSelection = {
      selectedRowKeys: studentAddedSelectedRowKey,
      onChange: (selectedRowKeys, selectedRows) => {
        const studentAddedSelectedTmp = {};

        selectedRows.forEach(selectedRow => {
          studentAddedSelectedTmp[selectedRow.studentId] = selectedRow;
        });
        this.setState({
          studentAddedSelected: studentAddedSelectedTmp,
          studentAddedSelectedRowKey: selectedRowKeys,
        });
      },
    };

    const enableAddBtn = () =>
      studentSelected && Object.values(studentSelected).length > 0 && hasLoadedAdded;
    const enableDeleteBtn = () =>
      studentAddedSelected && Object.values(studentAddedSelected).length > 0;
    const enablePrintAndExport = () => !!yearEduId && !!semesterId && !!feeObjectId;

    return (
      <Card>
        <Row>
          <SearchBar paramSearch={paramSearch} />
        </Row>
        <Row>
          <TableStudentData
            reload={reloadTable}
            service={getFeeObjectStudent}
            rowSelection={studentSelection}
            paramSearch={paramSearch}
            allowGetData={!!trainTypeId && !!facultyId && !!courseId}
          />
        </Row>
        <Row>
          <SearchBarStudentAdded paramSearch={paramSearch} />
        </Row>
        <Row id="btns-action">
          <BtnAddNew
            title="Thêm SV khoản thu"
            onClick={this.addStudent}
            disabled={!enableAddBtn()}
          />
          <BtnDelete
            title="Xóa SV khoản thu"
            onClick={this.removeStudent}
            disabled={!enableDeleteBtn()}
          />
          <Btn disabled={!enablePrintAndExport()} onClick={this.handleExportPdf}>
            <Icon type="printer" />
            In
          </Btn>
          <BtnExport disabled={!enablePrintAndExport()} onClick={this.handleExportExcel} />
        </Row>
        <Row>
          <TableStudentDataAdded
            reload={reloadTableAdded}
            service={this.getFeeObjectStudentAdded}
            rowSelection={studentAddedSelection}
            allowGetData={!!yearEduId && !!semesterId && !!feeObjectId}
            paramSearch={paramSearch}
          />
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FeeObjectStudent {...props} />
  </Page>
);



