import React, { Component } from 'react';
import { TableData } from 'components/common';

export default class TableStudentData extends Component {
  state = {};

  columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Giới tính',
      dataIndex: 'genderText',
      key: 'genderText',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
  ];

  render() {
    const { service, rowSelection, paramSearch, reload, allowGetData } = this.props;
    return (
      <TableData
        reload={reload}
        rowSelection={rowSelection}
        paramSearch={paramSearch}
        columns={this.columns}
        service={service}
        rowKey="studentId"
        allowGetData={allowGetData}
      />
    );
  }
}



